<template>
    <el-breadcrumb class="bar" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="encodeURI('/奥运风采展览馆')">
            奥运风采展览馆
        </el-breadcrumb-item>
        <el-breadcrumb-item v-for="(level, index) in levelList" :key="level + index" :to="level.path">
            {{ level.name }}
        </el-breadcrumb-item>
    </el-breadcrumb>
</template>

<script>
    export default {
        name: "RouterBar",
        data() {
            return {
                levelList: [],
            };
        },
        watch: {
            $route(route) {
                this.getBreadcrumb();
            },
        },
        created() {
            this.getBreadcrumb();
        },
        methods: {
            getBreadcrumb() {
                let temp = this.$route.path
                    .split("/")
                    .reduce((col, cur) => {
                        if (cur) {
                            cur = decodeURI(cur);
                            col.push({
                                name: cur,
                                path: col.reduce((a, b) => `${a.path}/${b}`, "") + "/" + cur,
                            });
                        }

                        return col;
                    }, [])
                    .map((i) => {
                        i.path = encodeURI(i.path);
                        return i;
                    });

                this.levelList = temp;
            },
        },
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    .bar {
        user-select: none;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 10px;
        cursor: pointer;
        font-size: 1.125rem;
        span:last-child {
            cursor: default;
            pointer-events: none;
        }
    }
</style>
